<template>
  <div class="app-container">
    <!-- ////////// 书架模块 ////////// -->
    <div v-show="!inReader" class="bookshelf">
      <div class="bookshelf-container">
        <!-- 书架标题栏 -->
        <div class="bookshelf-title">
          <span>我的书架</span>&emsp;
          <span v-show="keyword" style="font-size: 14px">当前搜索：{{ keyword }}</span>
          <!-- 按钮 更多 -->
          <el-popover popper-class="popover-more" placement="bottom-end" :width="160" :offset="10">
            <div class="bookshelf-menu-item" @click="sortByTime = true">
              <el-icon :style="{ top: '3px', visibility: sortByTime ? 'visible' : 'hidden' }"
                ><Select
              /></el-icon>
              <span style="right: 0">&nbsp;按访问时间排序</span>
            </div>
            <div class="bookshelf-menu-item" @click="sortByTime = false">
              <el-icon :style="{ top: '3px', visibility: sortByTime ? 'hidden' : 'visible' }"
                ><Select
              /></el-icon>
              <span style="right: 0">&nbsp;按添加顺序排序</span>
            </div>
            <template #reference>
              <el-icon :size="20"><MoreFilled /></el-icon>
            </template>
          </el-popover>
          <!-- 按钮 搜索 -->
          <el-popover popper-class="popover-more" placement="bottom-end" :width="200" :offset="10">
            <el-input
              v-model="searchInput"
              placeholder="书名搜索"
              clearable
              @input="inputChange"
            ></el-input>
            <template #reference>
              <el-icon :size="20"><Search /></el-icon>
            </template>
          </el-popover>
          <!-- 按钮 添加 -->
          <el-icon :size="20" @click="selectFile"><Plus /></el-icon>
        </div>
        <!-- 书架内容 -->
        <div class="bookshelf-content">
          <el-row :gutter="30">
            <!-- 书籍列表 -->
            <el-col
              v-for="item in bookListFilted"
              :key="item"
              :span="6"
              style="margin-bottom: 30px"
            >
              <el-card :body-style="{ padding: '0px', height: '220px', position: 'relative' }">
                <div class="cover-head">
                  <el-button
                    text
                    icon="Tickets"
                    class="cover-button"
                    @click="showCatalog(item)"
                  ></el-button>
                  <el-popconfirm title="确定删除吗?" @confirm="deleteBook(item)">
                    <template #reference>
                      <el-button text icon="Delete" class="cover-button"></el-button>
                    </template>
                  </el-popconfirm>
                </div>
                <img :src="require('./images/cover01.jpg')" class="image" />
                <div class="link-type book-name" @click="readBook(item)">
                  {{ item.bookName }}
                </div>
              </el-card>
            </el-col>
            <!-- 添加书籍 -->
            <el-col :span="6" style="margin-bottom: 30px">
              <el-card
                :body-style="{ padding: '0px', height: '220px', position: 'relative' }"
                @click="selectFile"
              >
                <el-icon :size="30" class="add-book-icon"><Plus /></el-icon>
              </el-card>
            </el-col>
          </el-row>
        </div>

        <br />

        <div v-show="catalogVisible">
          <!-- ////////// 页头 ////////// -->
          <el-page-header
            :icon="isAsc ? 'SortUp' : 'SortDown'"
            @back="sortChange"
            style="color: #4490f1; font-size: 16px"
          >
            <template #title>
              <span style="color: #4490f1; font-size: 16px"> {{ isAsc ? "升序" : "降序" }} </span>
            </template>
            <template #content>
              <div class="flex items-center">
                <span style="color: var(--el-text-color-regular); font-size: 16px">
                  {{ bookName }}
                </span>
                &emsp;
                <span
                  v-show="bookName != ''"
                  class="link-type"
                  style="font-size: 14px"
                  @click="readArticle()"
                  >继续阅读</span
                >
                &emsp;
                <span
                  class="link-type"
                  style="font-size: 14px; right: 0"
                  @click="catalogVisible = false"
                  >关闭</span
                >
              </div>
            </template>
            <!-- <template #extra>
              <el-button>关闭</el-button>
            </template> -->
          </el-page-header>
          <br />
          <!-- ////////// 表格 ////////// -->
          <el-table
            :key="tableKey"
            :data="list"
            border
            table-layout="fixed"
            :max-height="1000"
            style="width: 840px"
            @sort-change="sortChange"
          >
            <!-- 序号列 -->
            <el-table-column label="序号" width="60px" align="center">
              <template v-slot="props">
                {{ props.$index + (listPagination.pageIndex - 1) * listPagination.pageSize + 1 }}
                <!-- {{ props.$index + 1 }} -->
              </template>
            </el-table-column>
            <!-- 章节目录列 -->
            <el-table-column prop="title" label="章节目录" minWidth="160px" align="left">
              <template v-slot="{ row }">
                <span class="link-type" @click="readArticle(row)">
                  {{ row.title }}
                </span>
              </template>
            </el-table-column>
            <!-- 操作列 -->
            <el-table-column
              label="操作"
              align="center"
              width="120"
              class-name="small-padding fixed-width"
            >
              <template v-slot="{ row }">
                <el-button type="primary" size="small" @click="readArticle(row)"> 阅读 </el-button>
              </template>
            </el-table-column>
          </el-table>

          <!-- ////////// 底部分页栏 ////////// -->
          <pagination
            v-show="total > 0"
            :total="total"
            :autoScroll="false"
            v-model:page="listPagination.pageIndex"
            v-model:limit="listPagination.pageSize"
            @pagination="getList"
          />
        </div>
      </div>
    </div>

    <!-- ////////// 阅读模块 ////////// -->
    <YunReader
      v-show="inReader"
      ref="reader"
      :changeMode="changeMode"
      :inReader="inReader"
      :bookId="bookId"
      :bookName="bookName"
      :chapterList="chapterList"
      :articleData="articleData"
    />
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"
import YunReader from "./YunReader"
import { dbInit, dbOperation } from "@/utils/indexedDB"

export default {
  name: "BookShelf",
  components: { Pagination, YunReader },
  data() {
    return {
      fileList: [],
      bookList: [], //书籍列表（简要信息）
      sortByTime: true, //根据访问时间动态调整排序
      searchInput: "", //搜索框输入的内容
      searchTimer: null, //搜索防抖定时器
      keyword: "", //执行搜索的关键词
      bookText: "",
      chapterList: [], //章节数组{ id: 0, title: "", content: "" }
      bookName: "", //书名
      bookId: "", //书Id
      articleData: "", //{ id: "", title: "", content: "" },
      // 分页条件
      listPagination: {
        sort: "id", // 默认按id排序（首字母不要大写）
        sortType: "desc",
        pageIndex: 1,
        pageSize: 10,
      },
      tableKey: 0, //如果修改表格参数，key值加1即可更新表格显示
      list: [], //表格数据
      total: 0, //总行数
      isAsc: true, //表格是否升序
      inReader: false,
      catalogVisible: false,
    }
  },
  mounted() {
    const stores = [
      {
        storeName: "book",
        idName: "id",
        indexName: "bookId",
        propName: "bookId",
        unique: true,
      },
      {
        storeName: "brief",
        idName: "id",
        indexName: "bookId",
        propName: "bookId",
        unique: true,
      },
    ]
    dbInit("db_YunReader", 1, stores).then((response) => {
      console.log("db", response) //调试
      dbOperation("brief", "getAll").then((response) => {
        if (response) {
          // response.forEach((item) => {
          //   if (!item.accessTime) {
          //     dbOperation("brief", "put", {
          //       id: item.id,
          //       bookId: item.bookId,
          //       bookName: item.bookName,
          //       accessTime: new Date(),
          //     })
          //   }
          // })
          this.bookList = response
        }
      })
    })
  },
  methods: {
    extractTitles(str) {
      const reg =
        /(正文){0,1}(第)([零〇一二三四五六七八九十百千万a-zA-Z0-9]{1,7})[章节卷集部篇回]((?! {4}).)((?!\t{1,4}).){0,30}\r?\n/g
      let list = []
      let result = null
      result = str.match(reg)
      result && list.push(...result)
      for (let i = 0; i < list.length; i++) {
        list[i] = list[i].replace("\r", "").replace("\n", "").replace("\t", "")
      }
      return list
    },
    getChapters() {
      var timeStart = performance.now()
      const debug = false //调试总开关
      this.list = [] //清空
      // const regex =
      //   /^第(.*?)章|^第(.*?)回|^第(.*?)集|^第(.*?)卷|^第(.*?)部|^第(.*?)篇|^第(.*?)节|^第(.*?)季|^【(.*?)】/
      const regex =
        /^(第)([零〇一二三四五六七八九十百千万a-zA-Z0-9]{1,7})[章节卷集部篇回季](.){0,30}|^【(.{1,30})】$/
      const text = this.bookText
      // console.log(this.extractTitles(text))
      let paraList = text
        .replaceAll("\r\n", "\n")
        .replaceAll("\r", "\n")
        .split("\n")
        .map((item) => item.trim())
        .filter((item) => item.length > 0)
      // if (debug) console.log("paraList", paraList) //调试
      let start = 0
      let chapters = []
      let titles = []
      //按段落分析，生成临时的标题和章节数组
      for (let i = 0; i < paraList.length; i++) {
        //如果是标题
        // if (regex.test(paraList[i]) && paraList[i].length < 40) {
        if (regex.test(paraList[i])) {
          if (i == 0) {
            //如果第一段就是标题
            titles.push(paraList[i])
          } else {
            const content = paraList.slice(start, i)
            if (titles.length == 0 && start == 0) titles.push("前言")
            titles.push(paraList[i])
            chapters.push(content)
            start = i
          }
        }
        //如果到最后一段了
        if (i == paraList.length - 1) {
          const endChapter = paraList.slice(start, i + 1)
          if (titles.length == 0) titles.push(endChapter[0].slice(0, 10))
          chapters.push(endChapter)
        }
      }
      if (debug) console.log("chapters", chapters) //调试
      if (debug) console.log("titles", titles) //调试
      this.chapterList = [] //清空
      //生成最终的完整章节数据
      for (let i = 0; i < chapters.length; i++) {
        this.chapterList.push({
          id: i,
          title: titles[i],
          content: chapters[i],
          // content: chapters[i].join("\n"),
        })
      }

      this.updateBookList()

      var timeEnd = performance.now()
      console.log("getChapters 耗时", `${timeEnd - timeStart}ms`)
    },

    updateBookList() {
      dbOperation("brief", "getByIndex", { name: "bookId", value: this.bookId }).then(
        (response) => {
          if (!response) {
            const newBook = {
              bookId: this.bookId,
              bookName: this.bookName,
              chapterList: JSON.stringify(this.chapterList),
            }
            const newBrief = {
              bookId: this.bookId,
              bookName: this.bookName,
              accessTime: new Date(),
            }
            dbOperation("book", "add", newBook)
            dbOperation("brief", "add", newBrief)
            this.bookList.push(newBrief)
          } else {
            this.$message({ message: "选择的书已在列表中", type: "info", duration: 1000 })
          }
        }
      )
    },

    //阅读文章
    readArticle(row) {
      // this.articleData = row
      this.$refs.reader.turnLocked = true //翻页动画锁定
      // this.$refs.reader.currentPage = 0
      this.inReader = !this.inReader
      this.$nextTick(() => {
        if (row) {
          this.$refs.reader.currentChapter = row.id
        }
      })
      setTimeout(() => {
        this.$refs.reader.turnLocked = false //翻页动画打开
      }, 100)
    },
    readBook(item) {
      dbOperation("book", "getByIndex", { name: "bookId", value: item.bookId }).then((response) => {
        if (response) {
          dbOperation("brief", "put", {
            id: item.id,
            bookId: item.bookId,
            bookName: item.bookName,
            accessTime: new Date(),
          })
          item.accessTime = new Date()
          this.bookId = item.bookId
          this.bookName = response.bookName
          this.chapterList = JSON.parse(response.chapterList)
          this.listPagination.pageIndex = 1 //重置到列表第一页
          this.getList() //生成列表
          this.$refs.reader.turnLocked = true //翻页动画锁定
          this.inReader = !this.inReader
          setTimeout(() => {
            this.$refs.reader.turnLocked = false //翻页动画打开
          }, 100)
        }
      })
    },
    showCatalog(item, val = true) {
      this.catalogVisible = val
      if (val) {
        dbOperation("book", "getByIndex", { name: "bookId", value: item.bookId }).then(
          (response) => {
            if (response) {
              this.bookId = response.bookId
              this.bookName = response.bookName
              this.chapterList = JSON.parse(response.chapterList)
              this.listPagination.pageIndex = 1 //重置到列表第一页
              this.getList() //生成列表
            }
          }
        )
      }
    },
    // 删除书籍
    deleteBook(item) {
      dbOperation("brief", "getByIndex", { name: "bookId", value: item.bookId }).then(
        (response) => {
          //删除书籍最后位置、书签的记录，笔记暂不考虑在此处删除
          localStorage.removeItem("LASTPOSITION_" + item.bookId)
          localStorage.removeItem("BOOKMARKS_" + item.bookId)
          //刚添加的书没有id，所以获取数据库自动生成的id后再执行删除
          dbOperation("brief", "delete", response.id)
          dbOperation("book", "delete", response.id).then(() => {
            let index = 0
            for (let i in this.bookList) {
              if (this.bookList[i].id == item.id) {
                index = i
                break
              }
            }
            this.bookList.splice(index, 1)
          })
        }
      )
    },

    // el-upload选了文件
    // onChange() {
    //   this.$refs.upload.abort() //取消上传请求
    //   this.$refs.upload.clearFiles() //清空已上传的文件列表
    //   var event = event || window.event
    //   // var file = event.target.files[0]
    //   this.readFile(event.target)
    // },

    // 选择文件
    selectFile() {
      if (!window.FileReader) {
        alert("浏览器不支持浏览本地文件！")
        return
      }
      let inputObj = document.getElementsByClassName("_add-book-file")[0]
      if (!inputObj) {
        //如果input不存在则创建一个
        inputObj = document.createElement("input")
        inputObj.setAttribute("class", "_add-book-file")
        inputObj.setAttribute("type", "file")
        inputObj.setAttribute("style", "display:none")
        document.body.appendChild(inputObj)
        inputObj.onchange = () => {
          this.readFile(inputObj) //选中文件时触发
        }
      }
      inputObj.click()
      inputObj.value
    },
    // 读取文件
    readFile(input) {
      let file = input.files[0]
      document.body.removeChild(input) //读取完就移除，否则再选同一个文件会无效
      if (!file) return
      this.bookName = file.name.split(".").slice(0, -1).join(".")
      this.bookId = this.encode(this.bookName)
      this.checkFile(file)
      let reader = new FileReader()
      reader.onload = (e) => {
        const content = e.target.result
        // 使用FileReader以utf-8格式读取文件，根据文件内容是否包含乱码字符�，来判断文件是否为utf-8。
        // 如果存在�，即文件编码非utf-8，反之为utf-8。
        if (content.indexOf("�") === -1) {
          // console.log("UTF-8 , getChapters start")
          this.bookText = content
          this.getChapters()
        } else {
          // console.log("Not UTF-8, readAsText gb2312")
          reader.readAsText(file, "gb2312")
        }
      }
      reader.readAsText(file) //默认编码UTF-8
    },
    // 检查文件格式与大小
    checkFile(file) {
      const isTxt = file.type == "text/plain"
      const isLimit50M = file.size / 1024 / 1024 < 50
      // console.log(file.type)
      if (!isTxt) {
        this.$message.error("选择的文件只能是 txt 格式!")
      }
      if (!isLimit50M) {
        this.$message.error("选择的文件大小不能超过 50MB!")
      }
      return isTxt && isLimit50M
    },
    // 字符串转base64
    encode(str) {
      let encode = encodeURI(str) // 对字符串进行编码
      let base64 = btoa(encode) // 对编码的字符串转化base64
      return base64
    },
    // 改变模式（列表或阅读）
    changeMode() {
      this.inReader = !this.inReader
    },
    // 获取列表数据
    getList() {
      this.list = [] //清空
      this.total = this.chapterList.length
      const index = this.listPagination.pageIndex
      const size = this.listPagination.pageSize
      if (this.isAsc) {
        const start = (index - 1) * size
        const end = start + size
        for (let i = start; i < end && i < this.total; i++) {
          this.list.push({
            id: this.chapterList[i].id,
            title: this.chapterList[i].title,
          })
        }
      } else {
        const start = this.total - 1 - (index - 1) * size
        const end = start - size
        for (let i = start; i > end && i >= 0; i--) {
          this.list.push({
            id: this.chapterList[i].id,
            title: this.chapterList[i].title,
          })
        }
      }
    },
    // 目录顺序切换
    sortChange() {
      this.isAsc = !this.isAsc
      this.getList()
      this.listPagination.pageIndex = 1
    },
    // 搜索框内容变化
    inputChange(val) {
      clearTimeout(this.searchTimer)
      this.searchTimer = setTimeout(() => {
        this.keyword = val
      }, 500)
    },
  },
  computed: {
    // 过滤后的书籍列表
    bookListFilted() {
      const newList = this.bookList.filter((item) => {
        return this.keyword == "" || item.bookName.indexOf(this.keyword) > -1
      })
      //sort会改变原数组
      newList.sort((a, b) => {
        return this.sortByTime ? b.accessTime - a.accessTime : a.id - b.id
      })
      return newList
    },
  },
}
</script>

<style lang="scss">
:root {
  --text-hover-color: #20a0ff;
  --bg-hover-color: #eee;
}
// .popover-more {
//   //
// }
.bookshelf-menu-item {
  padding: 5px;
  text-align: center;
  &:hover {
    color: var(--text-hover-color);
    background-color: #eee;
    cursor: pointer;
  }
}
.app-container {
  padding: 20px;
  .bookshelf {
    overflow-x: auto;
    .bookshelf-container {
      width: 840px;
      margin: 0 auto;
      .bookshelf-title {
        color: #777777;
        background: linear-gradient(to right, #e6e6e6, #e1e1e1);
        padding: 10px 30px 10px 30px;
        border-radius: 10px 10px 0 0;
        .el-icon {
          float: right;
          top: -1px;
          margin-left: 20px;
          &:hover {
            color: var(--text-hover-color);
            cursor: pointer;
          }
        }
      }

      .bookshelf-content {
        padding: 30px 30px 0 30px;
        border: 1px dotted #cccccc;
        border-radius: 0 0 10px 10px;
        // box-shadow: inset 0 0 6px 2px rgb(150 150 150 / 50%);

        .el-card {
          background: #e7ecef;
          // border: 2px ridge #fff;
          border-radius: 5px;
          &:hover {
            box-shadow: 0 0 15px 10px rgb(150 150 150 / 30%);
            .cover-head {
              visibility: visible;
            }
          }
          .cover-head {
            visibility: hidden;
            position: absolute;
            left: 0;
            right: 0;
            .cover-button {
              color: #999999;
              font-size: 16px;
              // text-decoration: underline;
              // text-underline-offset: 2px;
              width: 36px;
              height: 30px;
              &:hover {
                color: var(--text-hover-color);
              }
              &:nth-child(2) {
                float: right;
                &:hover {
                  color: #ff0000;
                  background: #ffaaaa50;
                }
              }
            }
          }
          .image {
            width: 100%;
            display: block;
          }
          .book-name {
            font-size: 16px;
            text-align: center;
            height: 80px;
            padding: 12px;
          }
          .add-book-icon {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            color: #777777;
            &:hover {
              color: var(--text-hover-color);
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
</style>
