if (!window.indexedDB) {
  window.alert("你的浏览器不支持IndexedDB的稳定版本。书籍缓存功能将不可用。")
}

let db //全局IDBDatabase对象实例

/**
 * 开启一个读写的事务
 * @param {string} dbName 数据库名称
 * @param {number} dbVersion 数据库版本
 * @param {Array} stores 对象仓库数组
 * * [{
 * * * storeName: 对象仓库名称
 * * * idName: 主键名称
 * * * indexName: 索引名称
 * * * propName: 索引对应的属性名称(可以是数组)
 * * * unique: 是否可以重复
 * * },...],
 * @returns IDBDatabase对象实例
 */
export function dbInit(dbName = "db_default", dbVersion = 1, stores) {
  return new Promise((resolve, reject) => {
    //打开数据库
    const request = window.indexedDB.open(dbName, dbVersion)
    //成功事件，返回数据库对象实例（IDBDatabase对象的实例）
    request.onsuccess = function (event) {
      db = event.target.result
      resolve(event.target.result)
    }
    //数据库升级事件，仅在创建数据库或数据库升级时触发
    request.onupgradeneeded = function (event) {
      //缓存IDBDatabase接口，用于后续创建/删除对象存储空间（对象仓库，类比于关系型数据库的表）
      db = event.target.result
      //创建/删除对象存储空间的操作
      if (stores)
        stores.forEach((item) => {
          if (!db.objectStoreNames.contains(item.storeName)) {
            // 为数据库创建一个对象仓库
            const objectStore = db.createObjectStore(item.storeName, {
              keyPath: item.idName,
              autoIncrement: true,
            })
            //创建索引，第一个参数为索引名称，第二个参数为索引所在的属性，可以使用数组将多个属性设置为一个索引
            //第三个参数为可选参数，指定该属性的值是否要求唯一
            objectStore.createIndex(item.indexName, item.propName, { unique: item.unique })
          }
        })
    }
    request.onerror = function (err) {
      reject(err)
    }
  })
}

export async function dbOperation(store, method, data) {
  alert(store + "," + method + "," + data) //调试
  const methodList = ["add", "get", "getAll", "put", "delete", "getByIndex"]
  if (!methodList.includes(method)) {
    throw new Error(`操作类型错误, 仅支持: ${methodList.toString()} 方法`)
  }
  const readType = ["get", "getAll", "getByIndex"].includes(method) ? "readonly" : "readwrite"
  const objectStore = db.transaction(store, readType).objectStore(store)
  // 返回值
  return new Promise((resolve, reject) => {
    //获取IDBIndex对象，参数为索引的名称
    let request
    if (method == "getByIndex") {
      const index = objectStore.index(data.name)
      request = index.get(data.value)
    } else {
      request = objectStore[method](data)
    }
    alert(store + "," + method + "," + JSON.stringify(data) + ", request,", JSON.stringify(request))
    request.onsuccess = (res) => {
      alert(store + "," + method + "," + +JSON.stringify(data) + ", success,", res.target.result) //调试
      resolve(res.target.result)
    }
    request.onerror = (err) => {
      console.log(err) //调试
      reject(err)
    }
  })
}

